<template>
    <div class="container" :class="[
                state.phaseFade,
                state.mouseClass
            ]">
        <div class="content">
            <div id="trigendaBoard">
                <!-- Empty divs are used as containers for the conversation triangles. -->
                <div :class="{ highlight: state.tipIndex == 0 }"></div>
                <div :class="{ highlight: state.tipIndex == 2 }"></div>
                <div :class="{ highlight: state.tipIndex == 1 }"></div>
                <div :class="{ highlight: state.tipIndex == 3 }"></div>
            </div>
        </div>

        <!-- Show "Next stage" button if state.overlayIsActive is set to false -->
        <div class="content">
            <button v-if="!state.overlayIsActive" @click="activateOverlay">Seuraavaan vaiheeseen</button>
        </div>

        <Tip v-if="!state.backToStart" :tipMsg="state.tipMsg[state.tipIndex]" :mood="state.tipMood" />

        <!-- Overlay is a component where card type selection, card selection and answer choices are
        made for a current card placeholder shown in Phase2 component. Overlay is shown over the
        Phase2 component and Phase2 is shown transparent behind the Overlay.
        Overlay is shown or hidden based on state.overlayIsActive boolean value.
        Phase2 component keeps track of Overlays progress index and passes it to Overlay as prop.
        Overlay emits the info for deactivating the overlay to Phase2 and when the phase is finished.
        -->
        <Overlay :class="[state.overlayVisibility]"
            :progress="state.overlayProgress"
            @deactivateOverlay="deactivateOverlay"
            @phasecomplete="finishPhase" />
    </div>
</template>

<script>

import Card from "./Card.vue";
import Answer from "./Answer.vue";
import Overlay from "./Overlay.vue";
import Tip from './Tip.vue';
import { reactive } from "vue";

export default {
    name: "Phase2",
    props: {
        msg: String,
    },
    components: {
        Card,
        Answer,
        Overlay,
        Tip
    },
    setup(props, { emit } ) {

        const state = reactive({
            // Conditional CSS, when set to 'fadeIn', shows effect in template
            phaseFade: null,
            // Conditional CSS to disable 'mouseDisabled' or enable 'mouseActive' mouse clicks
            mouseClass: 'mouseDisabled',
            // Conditional CSS to hide 'hideOverlay' or show 'showOverlay' Overlay
            overlayVisibility: 'hideOverlay',
            // Index number for which state of the Overlay to show
            overlayProgress: 0,
            cardArray: JSON.parse(localStorage.getItem("cardArray")),
            tipMsg: [
                ['Olet nyt suorittanut keskustelun ensimmäisen vaiheen. Seuraavaksi valitset edellisen vaiheen ongelmista, haasteista tai toiveista kortin keskustelun tavoitteeksi.', 'Tavoite on asia, johon pyrimme keskustelun avulla löytämään ratkaisuja. Tästä eteenpäin pyydän sinua myös avaamaan valintojasi kirjoittamalla tekstikenttiin.'],
                ['Hienoa! Nyt keskustelulla on tavoite. Seuraavaksi valitsemme haasteen, joka hidastaa tämän tavoitteen toteutumista.', 'Sinulla on mahdollisuus valita joko ongelma, haaste tai toive. Tarvittaessa voit tutkia myös edellisen vaiheen "En osaa sanoa" -kortteja.'],
                ['Nyt valittuna ovat tavoite sekä haaste. Seuraavaksi alamme ratkomaan haastetta voimavarojasi käyttäen.', 'Valitse seuraavaksi ensimmäinen merkittävä voimavarasi. Tarvittaessa voit tutkia myös edellisen vaiheen "En osaa sanoa" -kortteja.'],
                ['Hyvä! Valitsethan vielä toisen voimavaran.'],
                [''],
            ],
            // Index of the object in tipMsg array which is passed to Tip component 
            tipIndex: 0,
            // Sets the mood of Tringo, passed to Tip component. 'happy', 'confused' or 'sad'
            tipMood: 'happy',
        });

        const fadeInElements = () => {
            state.phaseFade = 'fadeIn';
            setTimeout(() => {
                state.mouseClass = 'mouseActive';
            }, 1000);
        }

        fadeInElements();

        const activateOverlay = () => {
            state.overlayVisibility = 'showOverlay';
        }

        const deactivateOverlay = () => {
            state.overlayVisibility = 'hideOverlay';
            state.tipIndex++;
        }

        const finishPhase = () => {
            emit("phasecomplete");
        }
        return { activateOverlay, state, deactivateOverlay, finishPhase };
    },
    emits: ['phasecomplete'],
};

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
    margin: 40px 0 0;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}

.fadeIn {
    animation: fadeInAnimation ease 1s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.mouseActive {
    pointer-events: auto;
}

.mouseDisabled {
    pointer-events: none;
}

@keyframes fadeInAnimation {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
     }
}

.content {
    width: 100%;
    display: flex;
    justify-content: center;
}

.container:nth-of-type(1) {
    margin-top: 6em;
    margin-bottom: 2em;
}

.container:nth-of-type(2) {
    height: 40px;
}

#trigendaBoard {
    position: relative;
    align-items: start;
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(6, 1fr);
    grid-row-gap: 0px;
    grid-column-gap: 0px;
    min-width: 400px;
    max-width: 400px;
    justify-content: center;
}

#trigendaBoard div {
    padding: 5px;
    position: relative;
    text-transform: uppercase;
}

#trigendaBoard div:nth-of-type(1) {
    background-color: #7FFB6E;
    grid-column: 2 / 3;
    grid-row: 1;
}

#trigendaBoard div:nth-of-type(1)::before {
    content: 'Tavoite';
    position: absolute;
    top: 100px;
    right: 7px;
    bottom: 0;
    left: 0;
}

#trigendaBoard div:nth-of-type(2) {
    background-color: #60DAE2;
    grid-column: 1 / 2;
    grid-row: 2;
    left: 110px;
}

#trigendaBoard div:nth-of-type(2)::before {
    content: 'Voimavara';
    position: absolute;
    top: 100px;
    right: 7px;
    bottom: 0;
    left: 0;
}

#trigendaBoard div:nth-of-type(3) {
    background-color: #FEA35F;
    grid-column: 2 / 3;
    grid-row: 2;
    transform: rotate(180deg);
    right: 10px;
    top: -20px;
}

#trigendaBoard div:nth-of-type(3)::before {
    content: 'Haaste';
    position: absolute;
    top: 15px;
    right: 10px;
    bottom: 0;
    left: 0;
    height: 100px;
    transform: rotate(180deg);
}

#trigendaBoard div:nth-of-type(4) {
    background-color: #60DAE2;
    grid-column: 3 / 3;
    grid-row: 2;
    right: 110px;
}

#trigendaBoard div:nth-of-type(4)::before {
    content: 'Voimavara';
    position: absolute;
    top: 100px;
    right: 7px;
    bottom: 0;
    left: 0;
}

#trigendaBoard div {
    /*background-color: rgba(0,0,0,0.1);*/
    margin: 5px;
    height: 170px;
    width: 12em;
    clip-path: polygon(192px 170px, 96px 0, 0px 170px);
    border-radius: 2px;
}

.showOverlay {
    visibility: visible;
}

.hideOverlay {
    visibility: hidden;
}

.highlight {
    color: #fc555b;
    font-weight: bold;
}

button {
    margin-bottom: 15px;
}

@media only screen and (max-width: 500px) {
    #trigendaBoard div {
        transform: scale(0.9);
    }

    #trigendaBoard div:nth-child(1) {
        top: 15px;
        right: 1px;
    }

    #trigendaBoard div:nth-child(2) {
        left: 118px;
    }

    #trigendaBoard div:nth-child(3) {
        transform: scale(0.9) rotate(180deg);
    }

    #trigendaBoard div:nth-child(4) {
        right: 121px;
    }

    #trigendaBoard {
        padding-left: 10px;
    }
}
@media only screen and (max-width: 1300px) {
    .tipContainer {
        display: flex;
        position: absolute;
        width: 100vw;
        top: 600px;
        justify-content: center;
        bottom: 0;
    }

    #messageBox {
        position: relative;
        width: 60%;
    }
}
</style>
