<template>
    <div class="container">
        <img id="tringo" :src="state.tringoImage" />
        <div id="messageBox" :class="[state.tipVisibility]">
            <p v-for="message in tipMsg">{{ message }}</p>
        </div>
    </div>
</template>

<script>
import { reactive, watch } from 'vue';
import tringoHappy from '../assets/tringo_happy.png';
import tringoConfused from '../assets/tringo_confused.png';
import tringoSad from '../assets/tringo_sad.png';

export default {
    name: 'Tip',
    props: {
        tipMsg: Array,
        mood: String
    },
    setup(props) {

        /**
         * State object contains tringoImage which tells what imported
         * image to show in template. ShowTip is boolean value and
         * if the tipMsg prop is empty or null, showTip is set to false and
         * the message box is hidden by conditional CSS class.
         *
         * Watch functions are triggered when a change is made from the
         * parent component to props mood or tipMsg.
         */
        const state = reactive({ tringoImage: null, tipVisibility: null });

        watch(() => props.mood, (newValue) => {
            setImage(newValue);
        });

        watch(() => props.tipMsg, (newValue) => {
            setShowTip(newValue);
        });

        const setImage = (newMood) => {

            /**
             * Changes Tringo's image based on the value of
             * prop mood.
             */
            if (newMood === 'happy')
                state.tringoImage = tringoHappy;
            else if (newMood == 'confused')
                state.tringoImage = tringoConfused;
            else if (newMood === 'sad')
                state.tringoImage = tringoSad;
        }

        const setShowTip = (newTipMsg) => {

            /**
             * Sets state.showTip to true or false based on the
             * of prop tipMsg. If tipMsg is empty string or null,
             * set to false and the message box disappears.
             */
            if (newTipMsg !== null || newTipMsg !== '')
                state.tipVisibility = 'visibleMsg';
            else
                state.tipVisibility = 'hiddenMsg';
        }

        setImage(props.mood);
        setShowTip(props.tipMsg);

        return { state, setImage, setShowTip };
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h3 {
    margin: 40px 0 0;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}

p {
    padding: 0em 0.5em 0em 0.5em;
}

.container {
    position: absolute;
    width: 300px;
    left: 0;
    top: 12vh;
    z-index: 90;
}

#tringo {
    display: inline-block;
    width: 200px;
}

#messageBox {
    display: inline-block;
    position: absolute;
    text-align: left;
    border-radius: 5px;
    background-color: #ccc;
    padding: 0 10px;
    width: 100%;
}

.visibleMsg {
    display: inline;
}

.hiddenMsg {
    display: none;
}
@media only screen and (max-width: 1400px) {
    #messageBox {
        width: 75%;
    }
    #messageBox p{
        font-size: 15px;
    }
}
@media only screen and (max-width: 1300px) {
    #tringo {
        display: none;
    }

    .container {
        width: 100%;
        position: relative;
        top: 0;
    }

    #messageBox {
        width: 50%;
        position: static;
    }
}

@media only screen and (max-width: 800px) {
    .container {
        width: 100%;
        position: relative;
        top: 0;
    }

    #messageBox {
        width: 80%;
    }
}
</style>
